html {
    font-family: Arial, Helvetica, sans-serif;
    font-size: 1rem;
}

h1 {
    background: lightseagreen;
}

body, ul, h1, h2 {
    margin: 0;
    padding: 0;
}

nav {
    background: lightcoral;
    box-shadow: 0px 2px 10px -5px rgba(21, 6, 5, 1);
    padding: 20px 20px;
}

a {
    text-decoration: none;
}

.header_navigation_lists {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: space-between;
    align-items: center;
    list-style: none;
}